<template>
	<div>
		<div class="mp" v-for="(item,key) in items" >
			<p class="title" >
				<span style="color: silver;">》</span>
				<a href="javascript:" @click="newsList(key)">{{item.title}}</a>
				<hr style="color:#E0E0E0;" />
			</p>
			<ul>
				<li style="margin-bottom: 10px;" v-for="n in 2">
					<span style="color: silver;">>&nbsp;</span>
					<a href="javascript:" @click="getNews(key+'/'+item.secTitle[n-1])">{{item.secTitle[n-1]}}</a>
					<div style="max-height: 52px;margin-bottom:8px; overflow: hidden;">
						<p @click="getNews(key+'/'+item.secTitle[n-1])" style="line-height: 20px;width:86%;font-size: small;cursor: pointer;">
							{{item.content[n-1]}}
						</p>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<style type="text/css">
	.title {
		padding-left: 20px;
		font-size: larger;
		line-height: 15px;
	}
</style>

<script type="text/javascript">
	import $ from 'jquery'
	import {bus} from '../../js/bus.js'
	export default {
		data(){
			return {
				items:['']
			}
		},
		created() {
			this.$http.get('/index.php?c=index&a=index').then(response =>{
				this.items.splice(0,1,response.body)
			})
		},
		methods:{
			getNews:function(msg){
				bus.$emit('eleInfo',msg)
			},
			newsList:function(msg){
				bus.$emit('newsList')
				bus.$emit('getNewsList',msg)
			}
		}
	}
</script>


