<template>
	<div id="ss">
		<div v-for="item in items">{{item}}</div>
	</div>
</template>
	
<style type="text/css">
	.gotNewsList{
		width: initial;
		height: 100px;
		background-color: #F0F0F0;
	}
</style>

<script type="text/javascript">
	import Vue from 'vue'
	import {bus} from '../../js/bus.js'
	import $ from 'jquery'
	export default{
		data(){
			return {
				items:bus.newsItems
			}
		},
		created(){
			bus.$on('getNewsList',function(msg){
				$.ajax({
					type:"get",
					url:"./index.php?c=index&a=getNewsList",
					async:true,
					data:{"msg":msg},
					success:function(response){
						bus.newsItems.push(response)
					}
				});
			})
		}
	}
</script>


